body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  font-size: 16px;
  color: #FF0000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Courier New", Courier, monospace;
}

select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}
input[type=file] {
  width: 100%;
}
.Home .lander {
    padding: 5px 0;
    text-align: center;
  }
  
  .Home .lander h1 {
    font-family: "Courier New", Courier, monospace;
    font-weight: 600;
    color: red;
  }
  
  .Home .lander p {
    color: red;
  }
  
.NotFound {
    color:red;
    padding-top: 100px;
    text-align: center;
  }
  
@media all and (min-width: 480px) {
    .Login {
        color:red;
        padding: 60px 0;
    }
  
    .Login form {
        margin: 0 auto;
        max-width: 320px;
    }
  }
  
.App{
  margin-top: 15px;
  color: black;
}

.App .navbar-brand{
  font-weight: bold;
  color: #FF0000;
}

div{
  color: #FF0000;
}

.navbar{
  background-color: black
}
